  static urlParamsToDic(url: string): Record<string, string> {
    const queryParams: Record<string, string> = {};
    url.split('&').forEach(f => {
      const p = f.split('=');
      if (p.length === 2)
        queryParams[p[0]] = decodeURIComponent(p[1].replace(/\+/g, ' '));
    });
    return queryParams;
  }

  /** https://stackoverflow.com/questions/979975/how-to-get-the-value-from-the-get-parameters/1099670#1099670 */
  static getUrlParams(qs: string) {
    qs = qs.split('+').join(' ');

    const params: Record<string, string> = {};
    let tokens;
    const re = /[?&]?([^=]+)=([^&]*)/g;

    // tslint:disable-next-line:no-conditional-assignment
    while (tokens = re.exec(qs)) {
      params[decodeURIComponent(tokens[1])] = decodeURIComponent(tokens[2]);
    }

    return params;
  }

  static replaceUrlParam(url: string, paramName: string, paramValue: string) {
    if (paramValue === null) { paramValue = ''; }

    const pattern = new RegExp('\\b(' + paramName + '=).*?(&|#|$)');
    if (url.search(pattern) >= 0) { return url.replace(pattern, '$1' + paramValue + '$2'); }

    url = url.replace(/[?#]$/, '');
    return url + (url.indexOf('?') > 0 ? '&' : '?') + paramName + '=' + paramValue;
  }

  static getUrlPrefix(area: 'system' | 'zone' | 'app' | 'appShared', eavConfig: FormConfiguration) {
    let result = '';

    if (area === 'system') { result = eavConfig.systemRoot; } // used to link to JS-stuff and similar
    if (area === 'zone') { result = eavConfig.portalRoot; } // used to link to the site-root (like an image)
    if (area === 'app') { result = eavConfig.appRoot; } // used to find the app-root of something inside an app
    if (area === 'appShared') { result = eavConfig.appSharedRoot; } // used to find the app-root of something inside an app
    if (result.endsWith('/')) { result = result.substring(0, result.length - 1); }

    return result;
  }

  static newUrlIfCurrentContainsOldUrl(route: ActivatedRoute, oldEditUrl: string, newEditUrl: string) {
    const currentUrl = UrlHelpers.calculatePathFromRoot(route);
    const lastIndex = currentUrl.lastIndexOf(oldEditUrl);
    if (lastIndex <= 0) return null;
    const newUrl = currentUrl.substring(0, lastIndex) + currentUrl.substring(lastIndex).replace(oldEditUrl, newEditUrl);
    return newUrl;
  }

  static calculatePathFromRoot(route: ActivatedRoute) {
    let lastChild = route;
    while (lastChild.firstChild) {
      lastChild = lastChild.firstChild;
    }
    let pathFromRoot = '';
    for (const path of lastChild.snapshot.pathFromRoot) {
      if (path.url.length <= 0) continue;
      for (const urlSegment of path.url) {
        if (!urlSegment.path) continue;
        pathFromRoot += '/' + urlSegment.path;
      }
    }
    return pathFromRoot;
  }
